<template>
  <div id="app">
    <v-app id="customer">
      <div>
        <v-banner>
          <v-combobox class="combobox"
                      :items="getValidityOptions()"
                      :value="validity"
                      :full-width="false"
                      outlined
                      filled
                      dense
                      @change="onValidtiyChange"
          ></v-combobox>

          <v-combobox class="combobox"
                      :items="countries"
                      :value="country"
                      :full-width="false"
                      outlined
                      dense
                      filled
                      @change="onCountryChange"
          ></v-combobox>
        </v-banner>

        <v-container fill-height fluid>
          <v-row align="center"
                 justify="center">

            <v-data-table
                align="center"
                justify="center"
                :headers="headers"
                :items="customers"
                :options.sync="options"
                :server-items-length="totalCustomers"
                :loading="loading"
                outlined
                style="width: 80%;"
                class="elevation-1">

              <template v-slot:top>
                <v-toolbar flat style="background:#D3D3D3 ">
                  <v-toolbar-title class="tableHeader"> Customer's phonebook
                  </v-toolbar-title>
                </v-toolbar>
              </template>

              <template v-slot:item.valid="{ item }">
                <v-avatar :color="getColor(item.valid)" size="20"></v-avatar>
              </template>

            </v-data-table>
          </v-row>
        </v-container>
      </div>
    </v-app>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  name: 'CustomerGrid',
  data() {
    return {
      totalCustomers: 0,
      countries: ["None"],
      validity: "All",
      country: "None",
      customers: [],
      loading: true,
      options: {
        "country": null,
        "filterByValidity": false,
        "isValid": true,
        "itemsPerPage": 5,
        "page": 1
      },
      baseUrl: "http://localhost:8080/api",
      headers: [
        {text: 'Name', sortable: false, value: 'name'},
        {text: 'Phone', sortable: false, value: 'phone'},
        {text: 'Country', sortable: false, value: 'countryName'},
        {text: 'Validity', sortable: false, value: 'valid'}
      ],
    }
  },
  watch: {
    options: {
      handler() {
        this.loadData()
      },
      deep: true, // 2ay 7aga gowa el options et3'irt y7s bl change da
    },
  },
  mounted() {
    this.getCountryOptions();
    this.loadData();
  },
  methods: {
    loadData() {
      this.loading = true;
      this.getData();
    },
    getValidityOptions() {
      return ["All", "Valid", "Invalid"];
    },
    getCountryOptions() {
      this.loading = true;
      //then like on success catch like on failure
      axios.get(this.baseUrl + "/customers/getCountries").then(reponse => {
        this.countries = reponse.data
        this.countries.push("None") /*i want none to be at the begining*/

        this.loading = false;
      });
    },
    onValidtiyChange(value) {
      switch (value) {
        case "All":
          this.options.filterByValidity = false;
          break;
        case "Valid":
          this.options.filterByValidity = true;
          this.options.isValid = true;
          break;
        case "Invalid":
          this.options.filterByValidity = true;
          this.options.isValid = false;
          break;
      }
    },
    onCountryChange(value) {
      if (value == "None") {
        this.options.country = null;
      } else {
        this.options.country = value;
      }
      this.getData();
    },
    getData() {
      axios.post(this.baseUrl + "/customers/getCustomers", this.options).then(reponse => {
        this.customers = reponse.data.customers;
        this.totalCustomers = reponse.data.numberOfItems;
        this.loading = false;
      });
    },
    getColor(value) {
      if (value) {
        return "green";
      }
      return "red";
    }
  }
};
</script>
<style>
.combobox {
  float: right;
}

.tableHeader {
  color: blue;
  font-size: xx-large;
  font-weight: bold;
}
</style>
